<template>
    <div class="py-5">
        <h1 class="pb-2">Nuestro menú</h1>
        <p class="pb-4">Descubre todos nuestros productos y promociones.</p>

        <div class="mt-3">
            <b-button-group size="sm">
                <b-button variant="primary" href="#cafe">Cafés</b-button>
                <b-button variant="primary" href="#bolleria">Bollería</b-button>
                <b-button variant="primary" href="#promociones">Promociones</b-button>
            </b-button-group>
        </div>

        <h2 id="cafe" class="pt-5 pb-2">Cafés</h2>
        <div class="container-md">
            <b-card-group rows class="justify-content-center">
                <div rows cols="6" v-for="cafe in menu.cafes" :key="cafe.id">
                    <CardMenu
                        :nombre="cafe.nombre"
                        :alt="cafe.alt"
                        :precio="cafe.precio"
                        :imagen="cafe.imagen"
                    ></CardMenu>
                </div>
            </b-card-group>
        </div>

        <h2 id="bolleria" class="pt-5 pb-2">Bollería</h2>
        <div class="container-md">
            <b-card-group rows class="justify-content-center">
                <div rows v-for="bollo in menu.bolleria" :key="bollo.id">
                    <CardMenu
                        :nombre="bollo.nombre"
                        :alt="bollo.alt"
                        :precio="bollo.precio"
                        :imagen="bollo.imagen"
                        :apiId="bollo.apiId"
                    ></CardMenu>
                </div>
            </b-card-group>
        </div>

        <h2 id="promociones" class="pt-5 pb-2">Promociones</h2>
        <div class="container-md">
            <b-card-group rows class="justify-content-center">
                <div rows v-for="promo in menu.promociones" :key="promo.id">
                    <CardMenu
                        :nombre="promo.nombre"
                        :alt="promo.alt"
                        :precio="promo.precio"
                        :imagen="promo.imagen"
                    ></CardMenu>
                </div>
            </b-card-group>
        </div>
    </div>
</template>

<script>
import CardMenu from '../components/CardMenu.vue';

const menuJSON = '../jsons/menu.json';

export default {
    title: 'Nuestro menú',
    props: {
        name: {
            type: String,
            default: 'Menu',
        },
    },
    components: {
        CardMenu,
    },
    data() {
        return {
            menu: {},
        };
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        async fetchData() {
            const res = await fetch(menuJSON);
            const val = await res.json();
            this.menu = val;
        },
    },
};
</script>

<style scoped></style>
