<template>
    <div>
        <b-card
            :img-src="getImgUrl(imagen)"
            :img-alt="alt"
            :footer="precio + ' €'"
            min-cols="2"
            style="max-width: 30rem"
            class="m-3"
        >
            <b-card-text>
                {{ nombre }}
            </b-card-text>

            <span v-if="apiId">Calorías: {{ totalCalorias }} kCal</span>
        </b-card>
    </div>
</template>

<script>
const axios = require('axios');

export default {
    calorias: null,
    props: ['id', 'nombre', 'precio', 'imagen', 'alt', 'apiId'],
    data() {
        return {
            totalCalorias: null,
        };
    },
    methods: {
        getImgUrl(imgName) {
            return require('../assets/menu/' + imgName);
        },
    },
    created() {
        const headers = { Authorization: 'Token token=3d460f93515faf4373fc0354022ffda3' };

        if (this.apiId) {
            axios
                .get(`https://www.foodrepo.org/api/v3/products/${this.apiId}`, {
                    headers,
                })
                .then((response) => (this.totalCalorias = response.data.data.nutrients.energy_kcal.per_hundred));
        }
    },
};
</script>

<style scoped></style>
